/* eslint-disable no-underscore-dangle */
/* eslint-disable camelcase */
import { console } from '@splunk/dashboard-utils';
import get from 'lodash/get';
import { MetricsCollector, CollectorPayload } from './MetricsCollector';

type pusherFunction = (event: CollectorPayload) => void;

declare global {
    interface Window {
        _splunk_metrics_events?: { push: pusherFunction };
    }
}

export const NOT_REGISTERED_WARNING =
    'SWACollector was initialized, but SWA does not appear to be configured on the page.';

const isRegistered = (): boolean =>
    typeof get(globalThis, '_splunk_metrics_events.push') === 'function';

const isPerformanceEvent = (pageAction: string): boolean =>
    pageAction.substring(0, pageAction.indexOf('.')) === 'performance';
export class SWACollector implements MetricsCollector {
    constructor() {
        if (!isRegistered()) {
            console.warn(NOT_REGISTERED_WARNING);
        }
    }

    /**
     * Sends metrics data to a SWA metrics service
     * @param payload Processed data from the telemetry API
     */
    /* eslint-disable-next-line class-methods-use-this */
    sendEvent(payload: CollectorPayload): void {
        if (!isRegistered()) {
            throw new Error(NOT_REGISTERED_WARNING);
        }

        const { pageAction = '' }: { pageAction?: string } = payload.data;

        // extra `if` check to make the linter happy
        if (window._splunk_metrics_events && !isPerformanceEvent(pageAction)) {
            window._splunk_metrics_events.push({
                type: 'udf.telemetry',
                ...payload,
            });
        }
    }
}
