import * as React from 'react';
import TelemetryAPI from './TelemetryAPI';
import { MetricsCollector } from './MetricsCollector';

export type TelemetryContextProviderProps = {
    metricsCollectors?: MetricsCollector | MetricsCollector[];
    children: React.ReactNode;
};

const defaultTelemetryAPI = new TelemetryAPI();
const TelemetryContext = React.createContext(defaultTelemetryAPI);

const noCollectors: MetricsCollector[] = [];

const TelemetryContextProvider = ({
    metricsCollectors = noCollectors,
    children,
}: TelemetryContextProviderProps): React.ReactElement => {
    const value = React.useMemo(() => {
        let combinedMetricsCollectors: MetricsCollector[] = [];

        combinedMetricsCollectors = combinedMetricsCollectors.concat(
            metricsCollectors
        );
        const telemetryAPI = new TelemetryAPI({
            metricsCollectors: combinedMetricsCollectors,
        });
        return telemetryAPI;
    }, [metricsCollectors]);

    return (
        <TelemetryContext.Provider value={value}>
            {children}
        </TelemetryContext.Provider>
    );
};

const TelemetryContextConsumer = TelemetryContext.Consumer;

export {
    TelemetryContext as default,
    TelemetryContextProvider,
    TelemetryContextConsumer,
};
